import React, { useState } from 'react';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { AiOutlineRight } from 'react-icons/ai';
import TopPannel from './TopPanel';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { baseImgURL } from 'assets/Constant';

export default function Notifications(props) {
    const [notifData, setNotifData] = useState([]);
    const [loading, setLoading] = useState();

    React.useEffect(() => {
        setLoading(true)
        try {
            db.get(db.model.user).then(async (user) => {
                var res = await AdminServices.getPushNotifications(user.userData.id).then((res) => { return res })
                console.log(res.data);
                if (res.type) {
                    setNotifData(res.data?.reverse() || [])
                }
                setLoading(false)
            })
        } catch (error) {
            toast.error(error);
            setLoading(false)
        }
    }, [])

    // const getClientData = async (id) => {
    //     await db.get(db.model.clientData).then(async (p) => {
    //         if (p === null) {
    //             let res = await AdminServices.getClient(id).then(res => res);
    //             if (res.type === true) {
    //                 const a = JSON.parse(res.data);
    //                 let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
    //                 console.log("Get Client API :", aa)
    //                 setClientData(aa)
    //                 await db.set(db.model.clientData, JSON.stringify(aa))
    //             }
    //         } else {
    //             setClientData(p)
    //         }
    //     })
    // }

    return (
        <div className='mainContainer'>
            <MainHeader header='Notifications' navigation={props.history} />
            {
                loading ?
                    <ActivityLoaderMintbox />
                    :
                    <div className='dataContainer px-0'>
                        <TopPannel />
                        {
                            notifData.length === 0 ?
                                <>
                                    <div style={{ margin: "15% 8%", textAlign: 'center' }}>
                                        <img style={{ width: '120px', height: "120px" }}
                                            src={baseImgURL+"not-found.webp"} alt="" />
                                        <div className="bigHeader" style={{ marginTop: "24px", marginBottom: "40px" }}>No new notifications</div>
                                    </div>
                                </>
                                :
                                notifData.map(n => {
                                    const arr = n.AppURL.split('/')
                                    return <div className='menu-card' 
                                        // onClick={()=>{props.history.push(JSON.parse(n.WebURL))}}
                                        // onClick={()=>props.history.push({pathname:'/rebalance-screen',state:{type:arr[arr.length-2],code: arr[arr.length-1]}})}
                                        >
                                        <img src={baseImgURL+"menu_user.webp"} className='menu-card-icon' />
                                        <div className='menu-card-body'>
                                            <div className='bodyTextDark'>{n.Title.replace("{ en = ", "").replace(" }", "")}</div>
                                            <div className='smallTextLight'>{n.Content.replace("{ en = ", "").replace(" }", "")}</div>
                                            {
                                                n.ButtonText !== '' ?
                                                    <div className='header3dark secondaryGreen'>{n.ButtonText} <AiOutlineRight size={12} /></div>
                                                    :
                                                    <></>
                                            }
                                        </div>
                                    </div>
                                })
                        }
                    </div>
            }
        </div>
    )
}
